import { Component, OnInit } from '@angular/core';
import {Store} from '@ngrx/store';
import {AppState} from '../../../app.reducers';
import {InterventionByTheme, themeSearch} from '../../../interfaces/model';
import {ModalAttachmentPage} from '../../modal/modal-attachment/modal-attachment.page';
import {AlertController, ModalController, ToastController} from '@ionic/angular';
import {ModalInterveningPage} from '../../modal/modal-intervening/modal-intervening.page';
import {RestService} from '../../../service/rest.service';
import {ConfiguracionListaTheme, NAME_LIST_THEME_DELETE} from '../../../reducers/favThemes/theme.action';

@Component({
  selector: 'app-tab-temas',
  templateUrl: './tab-temas.page.html',
  styleUrls: ['./tab-temas.page.scss'],
})
export class TabTemasPage implements OnInit {
  themes: any;
  constructor(private storeThemes: Store<AppState>,
              private modalController: ModalController,
              private getRest: RestService,
              private alertController: AlertController,
              private toastController: ToastController) {
      this.storeThemes.select('listaThemes').subscribe( lista => {
          this.themes = lista;
      });
  }

  ngOnInit() {
  }
    async openAttachments(t: themeSearch) {
        const modal = await this.modalController.create({
            component: ModalAttachmentPage,
            componentProps: {
                "sessionId": t.agendaId,
                "themeId": t.id
            }
        });

        modal.onDidDismiss().then((dataReturned) => {
            if (dataReturned !== null) {
                // this.dataReturned = dataReturned.data;
            }
        });

        return await modal.present();
    }
    getIntervening(t: themeSearch) {
        this.getRest.getInterveningByTheme({
            themeId: t.id
        }).subscribe(response => {
            let lst: InterventionByTheme[];
            lst = response;
            if (lst.length > 0) {
                this.openIntervening(lst);
            } else {
                this.presentToast('No existen Intervenciones en este Tema');
            }
        });
    }
    async openIntervening(l: InterventionByTheme[]) {
        const modal = await this.modalController.create({
            component: ModalInterveningPage,
            componentProps: {
                "list": l
            }
        });
        return await modal.present();
    }
    removeTheme(item: themeSearch) {
      const deleteList = new ConfiguracionListaTheme(item.agendaId,
          item.agendaNumber, item.agendaStartDate, item.approvedOrder, item.dates, item.description, item.id);
      this.storeThemes.dispatch({type: NAME_LIST_THEME_DELETE, text: deleteList});
      this.presentToast('Tema borrado de favoritos');
    }
    async presentToast(msg) {
        const toast = await this.toastController.create({
            message: msg,
            duration: 2000,
            position: 'bottom'
        });
        toast.present();
    }

}
